  font-weight: bold;
  font-size: 20px;
  color: #d0af68;
}

.Hero .react-multi-carousel-list {
  height: 100vh;
  width: 100vw;
}
.Hero .react-multi-carousel-list img {
  width: 200px;
}

/* HeroSection.css */

.hero-section {
  background: rgba(255, 255, 255, 0) center/cover no-repeat;
  height: 100%; /* Adjust the height according to your design */
  display: flex;
  align-items: center;
}

.hero-content {
  text-align: center;
  color: #fff;
}

.hero-content h1 {
  font-size: 4.5rem;
  margin-bottom: 20px;
  text-transform: uppercase;
  color: transparent;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #dec284;
}

.hero-content p {
  font-size: 2rem;
  margin-bottom: 30px;
  color: transparent;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #d0af68;
}

.cta-buttons {
  display: flex;
  justify-content: center;
}

.cta-button {
  border-radius: 50rem;
  background-color: #fff0;
  color: #d0af68;
  padding: 10px 20px;
  margin: 0 10px;
  border: none;
  /* border-radius: 5px; */
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s ease;
  border: 1px solid #d0af68;
}

.cta-button:hover {
  background-color: #d0af68;
  color: #fff;
}
